






































































































































































































import pdf from "pdfvuer";
import Vue from "vue";
export default Vue.extend({
  name: "QDocumentViewer",
  components: {
    pdf,
  },
  props: {
    src: {
      default: "",
      type: [String, Object],
    },
    height: {
      type: String,
    },
    width: {
      type: String,
    },
    scale: {
      type: String,
    },
    showToolBar: {
      type: Boolean,
      default: true,
    },
    pdfToolbarBackgroundColor: {
      type: String,
      default: "#fafafa",
    },
    pdfBackgroundColor: {
      type: String,
      default: "#eeeeee",
    },
    hidePagination: {
      type: Boolean,
      default: false,
    },
    hideDescription: {
      type: Boolean,
      default: false,
    },
    hideDownload: {
      type: Boolean,
      default: false,
    },
    hideRotate: {
      type: Boolean,
      default: false,
    },
    hideZoom: {
      type: Boolean,
      default: false,
    },
    loadingContent: {
      type: String,
      default: "Loading...",
    },
    displayOnScroll: {
      type: Boolean,
      default: false,
    },
    fileDescription: {
      type: String,
    },
    _renderingProps: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      show: true,
      loadedRatio: 0,
      numPages: 0,
      page: 1,
      turn: 0,
      showToolbar: true,
      zoomVal:
        this.scale != undefined && this.scale != null
          ? Number(parseFloat(this.scale).toFixed(2))
          : 1.0,
      rotateVal: 0,
      loadedPages: 1,
      docType: undefined,
      url: this.src,
      localLanguage: "en",
      pageTitle: "Page",
      pdfKey: 0,
      currentMinWidth: 816,
    };
  },
  mounted() {
    this.docType = this.getType();
    let localLanguage = this._renderingProps
      .context()
      .GetItemSingle("Dory")
      .GetLRType()
      .substring(0, 2);
    if (localLanguage === "tr") {
      this.pageTitle = "Sayfa";
    } else {
      this.pageTitle = "Page";
    }
  },
  watch: {
    src: function () {
      this.pdfKey = new Date().valueOf();
      this.page = 1;
      this.loadedPages = 1;
      this.docType = this.getType();
    },
    scale: function () {
      this.zoomVal = Number(parseFloat(this.scale).toFixed(2));
    },
    page: function (newVal) {
      if (newVal === "") {
        return;
      }
      if (this.page > this.numPages) {
        this.page = this.numPages;
      }
      if (this.page < 1) {
        this.page = 1;
      }
      if (this.page <= this.numPages && this.page > this.loadedPages) {
        this.loadedPages = this.page;
      }
    },
    rotateVal: function (r) {
      this.rotateVal = this.rotateVal < 0 ? 270 : this.rotateVal;
    },
  },
  created() {
    this.onFrameScrollBottom();
  },
  destroyed() {
    this.$el?.remove();
  },
  methods: {
    createPdf() {
      let takenSrc;
      let isDataBase64Encoded = false;
      if (this.src.startsWith("data:application/pdf;base64,")) {
        takenSrc = this.src.split("data:application/pdf;base64,")?.[1];
        isDataBase64Encoded = true;
      } else if (this.src.startsWith("data:application/pdf,")) {
        takenSrc = this.src.split("data:application/pdf,")?.[1];
        isDataBase64Encoded = true;
      } else {
        takenSrc = this.src;
        isDataBase64Encoded = false;
      }
      if (isDataBase64Encoded) {
        const blob = this.base64ToBlob(takenSrc, "application/pdf");
        takenSrc = URL.createObjectURL(blob);
      }
      this.url = takenSrc;
      pdf.createLoadingTask(takenSrc).then((pdf) => {
        this.numPages = pdf.numPages;
      });
    },
    base64ToBlob(base64, type = "application/octet-stream") {
      const binStr = atob(base64);
      const len = binStr.length;
      const arr = new Uint8Array(len);
      for (let i = 0; i < len; i++) {
        arr[i] = binStr.charCodeAt(i);
      }
      return new Blob([arr], { type: type });
    },
    focusSelectedPage() {
      if (!this.displayOnScroll) {
        return;
      }
      if (this.page > this.numPages) {
        this.page = 1;
        var focusedPage = document.getElementById("pdf-page-1");
        if (focusedPage) {
          focusedPage.scrollIntoView();
        }
      } else {
        var focusedPage = document.getElementById(
          "pdf-page-" + this.page.toString()
        );
        if (focusedPage) {
          focusedPage.scrollIntoView();
        }
      }
    },
    zoomIn() {
      if (this.zoomVal < 2) {
        this.zoomVal += 0.1;
        this.currentMinWidth += 81.6;
      }
    },
    zoomOut() {
      if (this.zoomVal > 0.2) {
        this.zoomVal -= 0.1;
        this.currentMinWidth -= 81.6;
      }
    },
    nextPage() {
      this.page += this.page < this.numPages ? 1 : 0;
    },
    previousPage() {
      this.page -= this.page > 0 ? 1 : 0;
    },
    rotate() {
      this.rotateVal -= 90;
    },
    goToPage(pageNumber: number) {
      if (pageNumber >= 1 && pageNumber <= this.numPages) {
        this.page = pageNumber;
      }
    },
    downloadPdf() {
      fetch(this.url)
        .then((res) => res.blob()) // Gets the response and returns it as a blob
        .then((blob) => {
          let objectURL = URL.createObjectURL(blob);
          var link = document.createElement("a");
          link.href = objectURL;
          link.download = this.fileDescription ?? "fileData"; //Name of the file to be downloaded
          link.dispatchEvent(new MouseEvent("click"));
        });
    },
    getType() {
      const val = this.src;
      if (val?.startsWith("data:")) {
        const mediaType = val
          .split(",")[0]
          .split(";")[0]
          .split(":")[1]
          .split("/");
        if (mediaType[0] === "image") {
          return "img";
        } else if (mediaType[1] === "pdf") {
          this.createPdf();
        }
        return mediaType[1];
      } else {
        const parsedType = val.split(".").pop();
        if (["html", "htm"].includes(parsedType)) {
          return "html";
        } else if (
          ["jpeg", "jpg", "gif", "png", "apng", "svg", "bmp", "ico"].includes(
            parsedType
          )
        ) {
          return "img";
        } else if (["pdf"].includes(parsedType)) {
          this.createPdf();
          return parsedType;
        }
        return parsedType;
      }
    },
    onFrameScrollBottom() {
      const self = this;
      let scrolledToBottom = (e) => {
        if (e.data == "scrolledToBottom") {
          self.$emit("scrolledToBottom");
        }
      };
      window.removeEventListener("message", scrolledToBottom, false);
      window.addEventListener("message", scrolledToBottom, false);
    },
    handleScroll({
      target: {
        scrollTop,
        clientHeight,
        scrollHeight,
        lastChild: { offsetHeight },
      },
    }) {
      if (this.displayOnScroll && this.showToolBar) {
        let pageVal = parseInt(
          Math.ceil(Math.round(scrollTop) / offsetHeight).toString()
        );

        this.page =
          pageVal == 0 ? 1 : pageVal >= this.numPages ? this.numPages : pageVal;
      }
      if (Math.round(scrollTop) + clientHeight + 1 >= scrollHeight) {
        if (this.displayOnScroll && this.numPages > this.loadedPages) {
          this.loadedPages++;
        }
        this.$emit("scrollEnd");
      }
    },
  },
});
